import colors from '../colors'
import posed from "react-pose";

const Square = posed.div({
  idle: { scale: 1 },
  hovered: { scale: 1.05 }
});

class Member extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      h: false,
    }
  }

  render() {
    return(
      <Square
        pose={this.state.hTeam ? "hovered" : "idle"}
        onMouseEnter={() => this.setState({ h: true })}
        onMouseLeave={() => this.setState({ h: false })}>
        {(this.state.h ? <div style={{height: 210, width: 210, borderRadius: 105, backgroundColor: colors.b4}}></div> : <div style={{height: 200, width: 200, borderRadius: 100, backgroundColor: colors.b3}}></div>)}
      </Square>
    );
  }
}

export default Member;
